import React from 'react';
import web from '../src/images/img.svg';
import { NavLink } from 'react-router-dom';

const Common = (props)=>{
  return (
    <>

      <section id="header" className="mt-5 d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">

              <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                <h1> {props.name}  </h1>
                <h2 style={{fontSize:'24px'}} className="my-3">We are here to scale your business </h2>
                <div className="mt-3">
                  <NavLink className="btn btn-get-started" to={props.visit}>{props.btnName}</NavLink>
              </div>
              </div>

              <div className="col-lg-6 order-1 order-lg-2 header-image mt-5">
                <img src={props.imgsrc} className="img-fluid animated" alt="home-img" />
              </div>

              </div>
            </div>
          </div>
        </div>
      </section>

    </>
  );
}

export default Common ;